.osc-idea-details {
  width: 700px;
  margin: 0 auto;
  background-color: white;
}
@media only screen and (max-width: 700px) {
  .osc-idea-details {
    padding: 0 15px;
    width: 100%;
  }
  .osc-idea-details h2 {
    margin-top: 10px;
  }
}
.osc-idea-details .osc-details-image-and-stats {
  position: relative;
}
.osc-idea-details .osc-details-image-and-stats .osc-image-container {
  position: relative;
  width: 700px;
  height: 393px;
  overflow: hidden;
}
@media only screen and (max-width: 700px) {
  .osc-idea-details .osc-details-image-and-stats .osc-image-container {
    width: 100%;
    height: auto;
    padding-top: 56.25%;
    /* 16:9 aspect ratio */
  }
}
.osc-idea-details .osc-details-image-and-stats .osc-image-container .osc-image {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-size: cover;
  background-repeat: no-repeat;
}
.osc-idea-details .osc-details-image-and-stats .ocs-idea-label {
  position: absolute;
  top: 10px;
  left: 0;
  padding: 4px 15px;
  font-weight: bold;
}
.osc-idea-details .osc-details-image-and-stats .osc-details-stats {
  position: relative;
  left: 0;
  width: 200px;
  height: auto;
  margin-top: 10px;
  margin-bottom: 20px;
}
@media only screen and (max-width: 700px) {
  .osc-idea-details .osc-details-image-and-stats .osc-details-stats {
    width: 100%;
  }
}
.osc-idea-details .osc-details-image-and-stats .osc-details-stats .osc-vote-button {
  box-sizing: border-box;
  text-decoration: none;
  display: block;
  margin-bottom: 20px;
  height: 43px;
  margin-bottom: 4px;
}
.osc-idea-details .osc-details-image-and-stats .osc-details-stats .osc-vote-button .osc-number-button-text {
  background-position: 8px 6px;
  background-size: auto 28px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  padding-left: 50px;
}
.osc-idea-details .osc-details-image-and-stats .osc-details-stats .osc-vote-button .osc-number-button-text.ocs-user-has-voted {
  background-color: #BED200 !important;
}
.osc-idea-details .osc-details-image-and-stats .osc-details-stats .osc-vote-button .osc-number-button-text.ocs-busy {
  background-color: #9a9a9a !important;
}
.osc-idea-details .osc-details-image-and-stats .osc-details-stats .osc-no-of-reactions {
  background-position: center left 0px;
  background-size: 20px 15px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  color: #004699;
  padding-left: 30px;
  line-height: 25px;
}
.osc-idea-details .osc-details-sharebuttons {
  width: 700px;
  margin: -5px 0 20px 0;
}
@media only screen and (max-width: 700px) {
  .osc-idea-details .osc-details-sharebuttons {
    width: 100%;
  }
}
.osc-idea-details .osc-details-sharebuttons ul {
  display: inline-block;
  padding: 0;
  height: 30px;
  list-style-type: none;
}
.osc-idea-details .osc-details-sharebuttons ul li {
  display: inline-block;
  width: 34px;
  height: 30px;
}
.osc-idea-details .osc-details-sharebuttons ul a {
  display: block;
  font-size: 0;
  line-height: 0;
  width: 30px;
  height: 30px;
  margin: 0 4px 0 0;
}
.osc-idea-details .osc-details-sharebuttons ul a.osc-share-email {
  background-position: cover;
  background-size: 30px 30px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
}
.osc-idea-details .osc-details-sharebuttons ul a.osc-share-facebook {
  background-position: cover;
  background-size: 30px 30px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
}
.osc-idea-details .osc-details-sharebuttons ul a.osc-share-twitter {
  background-position: cover;
  background-size: 30px 30px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
}
.osc-idea-details .osc-details-sharebuttons ul a.osc-share-whatsapp {
  background-position: cover;
  background-size: 30px 30px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
}
.osc-idea-details .osc-details-vote-buttons-container {
  margin-bottom: 20px;
}
.osc-idea-details .osc-details-summary,
.osc-idea-details .osc-details-description {
  width: 700px;
}
@media only screen and (max-width: 700px) {
  .osc-idea-details .osc-details-summary,
  .osc-idea-details .osc-details-description {
    width: 100%;
  }
}
.osc-idea-details .osc-details-summary {
  font-weight: 600;
}
.osc-idea-details .osc-modbreak {
  position: relative;
  width: 700px;
  margin: 25px 0;
  padding: 20px;
  border: 3px solid #FFE115;
}
@media only screen and (max-width: 700px) {
  .osc-idea-details .osc-modbreak {
    width: 100%;
  }
}
.osc-idea-details .osc-modbreak:after {
  border-top: 3px solid #FFE115;
  border-right: 3px solid #FFE115;
  border-bottom: 0;
  border-left: 0;
  position: absolute;
  width: 30px;
  height: 30px;
  right: 4px;
  top: -19px;
  background-color: white;
  transform: rotate(-45deg);
  content: '';
}
