.osc-align-right-container {
  text-align: right;
}
.osc-margin-right {
  margin-right: 15px;
}
.osc-opacity-25 {
  opacity: 0.25;
}
.osc-opacity-50 {
  opacity: 0.5;
}
.osc-opacity-65 {
  opacity: 0.65;
}
.osc-button {
  position: relative;
  display: inline-block;
  padding: 12px 15px;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  border: 0 none;
  cursor: pointer;
  background-color: #c4c4c4;
}
.osc-button:active,
.osc-button:focus {
  outline: 0;
}
.osc-button.osc-disabled {
  opacity: 0.3;
}
.osc-button:hover {
  text-decoration: underline;
  background-color: #aaa;
}
.osc-close-button-black {
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  position: absolute;
  top: 20px;
  right: 15px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: 0 none;
  cursor: pointer;
  z-index: 20;
}
.osc-close-button-black:active,
.osc-close-button-black:focus {
  outline: 0;
}
.ocs-gray-text {
  color: #9a9a9a;
}
.osc-not-logged-in-button {
  background-position: center left 10px;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  padding-left: 40px;
}
.osc-default-select {
  background-position: center right 10px;
  background-size: 13px 9px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: 44px;
  padding: 0 20px;
  padding-right: 40px;
  border: solid 1px #434343;
  outline: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 44px;
  text-align: left;
  background-color: white;
}
.osc-default-input {
  appearance: none;
  height: 44px;
  padding: 0 20px;
  padding-right: 40px;
  border: solid 1px #434343;
  outline: none;
  font-size: 16px;
  font-weight: 400;
  line-height: 44px;
  text-align: left;
  background-color: white;
}
.osc-clear-both {
  clear: both;
}
.osc-number-button {
  display: block;
  width: 250px;
  font-size: 0px;
  font-weight: 600;
  text-decoration: none;
  height: 43px;
  margin-right: 2px;
}
.osc-number-button .osc-number-plates {
  padding: 7px 6px 7px 10px;
  font-size: 0px;
  margin-right: 0px;
  float: left;
}
.osc-number-button .osc-number-plate {
  margin-right: 4px;
  font-size: 18px;
  background: #e8e8e8;
  background: linear-gradient(#e8e8e8 50%, #ffffff 50%);
  padding: 0;
  display: inline-block;
  height: 29px;
  line-height: 31px;
  width: 20px;
  text-align: center;
  color: black;
  border-radius: 3px;
  font-weight: 800;
  vertical-align: middle;
}
.osc-number-button .osc-number-button-text {
  position: relative;
  display: inline-block;
  padding: 12px 15px;
  font-size: 16px;
  font-weight: 600;
  border: 0 none;
  cursor: pointer;
  display: block;
  float: left;
  width: 118px;
  height: 43px;
  margin-left: 2px;
  font-size: 18px;
  line-height: 20px;
}
.osc-number-button .osc-number-button-text:active,
.osc-number-button .osc-number-button-text:focus {
  outline: 0;
}
.osc-number-button .osc-number-button-text.osc-disabled {
  opacity: 0.3;
}
.osc-number-button .osc-number-button-text:hover {
  text-decoration: underline;
}
.osc-choices-guide-editbutton,
.osc-idea-details-editbutton {
  display: block;
  position: relative;
  appearance: none;
  width: 100%;
  height: 46px;
  margin-top: 20px;
  margin-left: 0;
  padding-left: 46px;
  padding-right: 3px;
  font-size: 16px;
  font-weight: 500;
  text-overflow: ellipsis;
  text-align: center;
  line-height: 44px;
  border-radius: 0;
  border-right: 2px solid #e8e8e8;
  border-bottom: 2px solid #e8e8e8;
  background-color: whitesmoke;
  background-position: 5px 5px;
  background-repeat: no-repeat;
  background-size: 36px 36px;
  color: black;
  overflow: hidden;
  cursor: pointer;
}
.osc-choices-guide-editbutton:after,
.osc-idea-details-editbutton:after {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 34px;
  height: 34px;
  background-color: black;
  content: '';
}
.osc-choices-guide-editbutton.osc-delete:after,
.osc-idea-details-editbutton.osc-delete:after {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-choices-guide-editbutton.osc-edit:after,
.osc-idea-details-editbutton.osc-edit:after {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul {
  margin: 10px 0 0 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
}
.osc-sharebuttons ul li {
  display: inline;
  height: 30px;
  width: 30px;
  margin: 0;
  padding: 0;
}
@media only screen and (max-width: 767px) {
  .osc-sharebuttons ul li.osc-desktop {
    display: none;
  }
}
@media only screen and (min-width: 768px) {
  .osc-sharebuttons ul li.osc-mobile {
    display: none;
  }
}
.osc-sharebuttons ul li a {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 30px 30px;
  display: block;
  float: left;
  font-size: 0;
  line-height: 0;
  margin: 0 3px 0 0;
  height: 30px;
  width: 30px;
}
.osc-sharebuttons ul li a.osc-facebook {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul li a.osc-facebook:hover {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul li a.osc-twitter {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul li a.osc-twitter:hover {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul li a.osc-email {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul li a.osc-email:hover {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul li a.osc-whatsapp {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-sharebuttons ul li a.osc-whatsapp:hover {
  background-image: url(data:image/svg+xml;base64,);
}
.osc-tabs {
  width: 100%;
  height: 44px;
  margin-top: 40px;
  border-bottom: 2px solid #e6e6e6;
}
.osc-tabs .osc-tab {
  float: left;
  overflow: hidden;
  height: 44px;
  margin-right: 30px;
  font-size: 18px;
  font-weight: 600;
  line-height: 44px;
  cursor: pointer;
}
.osc-tabs .osc-tab:hover {
  border-bottom: 6px solid black;
}
.osc-tabs .osc-tab.osc-active {
  border-bottom: 6px solid #ec0000;
}
.osc-component {
  box-sizing: border-box;
  font-size: 16px;
  font-weight: normal;
  line-height: 20px;
}
.osc-component h3 {
  font-size: 24px;
  font-weight: 600;
  font-style: normal;
  line-height: 1.25;
}
.osc-component a {
